*{
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}
.App{
  display: flex;
  
}
nav{
  background-color: gray;
}
nav ul{
  text-align: center;
  list-style: none;
}
nav ul li {
  margin: 40px;
}
nav ul li a{
  color: whitesmoke;
  text-decoration: none;
}
.home{
  display: flex;
  justify-content: center;
  flex-direction: column;
  align-items: center;
  width: 100%;
  padding:30px 0 60px 0 ;
  background-color: whitesmoke;
}
.type{
  font-size: 50px;
}
.home img{
  width: 400px;
  border-radius: 1%;
}
