.contact{
    display: flex;
    justify-content: flex-start;
    flex-direction: row;
    align-items: center;
    grid-template-columns: 50% 50%;
    width: 100%;
    padding:30px 0 120px 145px ;
    background-color: whitesmoke;
  }
  .contact img{
      width: 400px;
      border-radius: 1%;

  }
  .contact_data{
    margin: 3% 0 3% 5% ;
    padding: 10%;
  }
  .contact_data p{
    margin: 2% 0 2% 0;
  }