.skill{
    display: flex;
    justify-content: flex-start;
    flex-direction: row;
    align-items: center;
    grid-template-columns: 50% 50%;
    width: 100%;
    padding:30px 0 120px 145px ;
    background-color: whitesmoke;
}
.skill img{
    width: 400px;
    border-radius: 1%;
}
.skill_data{
    margin: 3% 0 3% 5% ;
    padding: 0 0 0 10%;
}

.skill_data ul{
    margin: 2%;
}
